import "./final.css";
import Board from "../board/Board";
import { useState } from "react";
import { Button } from "antd";

const FinalInfo = ({
  valueWidth,
  valueLength,
  valueQuantity,
  valueQuantityNeed,
  valueQuantityArr,
  valueArr,
}) => {
  const [showBoard, setShowBoard] = useState(false);

  const showByClick =  () => {
    setShowBoard(true)
  }
  const closeByClick =  () => {
    setShowBoard(false)
  }
  const width = valueWidth;
  const length = valueLength;
  const quantity = valueQuantity;
  return (
    <div className="global-final-info">
      <h2> </h2>
      <p>
        <span>   /</span>
        {width} 
      </p>
      <p>
        <span>  /</span>
        {length} 
      </p>
      <p>
        <span>  /</span>
        {quantity} 
      </p>
      <p>
        <span>   /</span>
        {valueQuantityNeed}
      </p>
      <Button  onClick={showByClick} className="form-button">
            
      </Button>
      <Button onClick={closeByClick}></Button>
      {showBoard &&   <Board
        arr={valueArr}
        Length={valueLength}
        Width={valueWidth}
        quantityDiv={valueQuantityArr}
      />}
    
    </div>
  );
};

export default FinalInfo;
